// 头像库模块
// 从预定义的世界书条目"[Data]头像库数据"读取头像数据
// 这个世界书条目设置为绿灯禁用，不会进入AI提示词

export interface AvatarLibrary {
  [characterName: string]: {
    [avatarId: number]: string;
  };
}

// 默认头像库数据，当世界书条目不存在时作为fallback
const defaultAvatarLibrary: AvatarLibrary = {
  default: {
    0: '',
    1: 'https://via.placeholder.com/400x600?text=角色',
    2: 'https://via.placeholder.com/400x600?text=魔法少女',
    3: 'https://via.placeholder.com/400x600?text=变身',
  },
  星原樱: {
    0: '',
    1: 'https://via.placeholder.com/400x600?text=星原樱',
    2: 'https://via.placeholder.com/400x600?text=魔法少女-樱',
    3: 'https://via.placeholder.com/400x600?text=神社巫女-樱',
  },
};

let avatarLibrary: AvatarLibrary = { ...defaultAvatarLibrary };
let isInitialized = false;

/**
 * 从世界书条目加载头像库数据
 * 世界书条目名称: "[Data]头像库数据"
 * 条目内容格式:
 *   头像库数据:
 *     角色名:
 *       0: ""
 *       1: "头像URL"
 */
async function initAvatarLibrary() {
  if (isInitialized) return;

  try {
    // 获取当前角色卡的世界书名称
    const worldInfoName = await getCurrentWorldInfoName();
    if (!worldInfoName) {
      console.warn('无法获取当前世界书名称，使用默认头像库');
      isInitialized = true;
      return;
    }

    // 从世界书加载数据
    const worldInfo = await SillyTavern.loadWorldInfo(worldInfoName);
    if (worldInfo && Array.isArray(worldInfo.entries)) {
      // 查找"[Data]头像库数据"条目
      const avatarEntry = worldInfo.entries.find(
        (e: any) => e.key && e.key.some((k: string) => k.includes('头像库数据')),
      );

      if (avatarEntry && avatarEntry.content) {
        try {
          // 解析YAML内容
          const content = avatarEntry.content;
          // 提取头像库数据部分
          const dataMatch = content.match(/头像库数据:\s*([\s\S]*?)(?=\n\n|$)/);
          if (dataMatch) {
            const parsed = parseAvatarData(content);
            if (parsed) {
              avatarLibrary = { ...defaultAvatarLibrary, ...parsed };
              console.log('从世界书条目"[Data]头像库数据"加载头像库成功');
            }
          } else {
            console.log('世界书条目内容格式不正确，使用默认头像库');
          }
        } catch (parseError) {
          console.error('解析头像库数据失败:', parseError);
        }
      } else {
        console.log('世界书条目中未找到头像库数据，使用默认头像库');
      }
    } else {
      console.log('世界书不存在或为空，使用默认头像库');
    }
  } catch (error) {
    console.error('加载头像库失败:', error);
  }

  isInitialized = true;
}

/**
 * 获取当前角色卡的世界书名称
 */
async function getCurrentWorldInfoName(): Promise<string | null> {
  try {
    // 使用酒馆助手提供的getCharacter接口获取当前角色卡信息
    const character = await getCharacter('current');
    if (character && character.worldbook) {
      return character.worldbook;
    }

    // 如果无法获取，返回null
    return null;
  } catch (error) {
    console.error('获取当前世界书名称失败:', error);
    return null;
  }
}

/**
 * 解析头像库数据
 * 将YAML格式的内容解析为AvatarLibrary对象
 */
function parseAvatarData(content: string): AvatarLibrary | null {
  try {
    const result: AvatarLibrary = {};

    // 简单的YAML解析：提取角色名和头像ID-URL映射
    const lines = content.split('\n');
    let currentCharacter: string | null = null;

    for (const line of lines) {
      const trimmedLine = line.trim();

      // 跳过注释和空行
      if (!trimmedLine || trimmedLine.startsWith('#')) continue;

      // 检测角色名（缩进为2个空格，以字母或中文开头）
      const charMatch = line.match(/^  ([\u4e00-\u9fa5a-zA-Z][^:]+):/);
      if (charMatch && !line.includes('http')) {
        currentCharacter = charMatch[1].trim();
        result[currentCharacter] = {};
        continue;
      }

      // 检测头像ID和URL（缩进为4个空格）
      const avatarMatch = line.match(/^    (\d+):\s*"(.*)"/);
      if (avatarMatch && currentCharacter) {
        const avatarId = parseInt(avatarMatch[1]);
        const url = avatarMatch[2];
        result[currentCharacter][avatarId] = url;
      }
    }

    return Object.keys(result).length > 0 ? result : null;
  } catch (error) {
    console.error('解析头像数据失败:', error);
    return null;
  }
}

/**
 * 保存头像库数据到世界书条目
 * 注意：这会更新世界书条目的内容
 */
export async function saveAvatarLibrary(): Promise<void> {
  try {
    const worldInfoName = await getCurrentWorldInfoName();
    if (!worldInfoName) {
      console.error('无法获取当前世界书名称，无法保存头像库');
      return;
    }

    const worldInfo = await SillyTavern.loadWorldInfo(worldInfoName);
    if (!worldInfo || !Array.isArray(worldInfo.entries)) {
      console.error('世界书不存在，无法保存头像库');
      return;
    }

    // 查找或创建头像库数据条目
    const entryIndex = worldInfo.entries.findIndex(
      (e: any) => e.key && e.key.some((k: string) => k.includes('头像库数据')),
    );

    // 将头像库数据转换为YAML格式
    const yamlContent = convertToYaml(avatarLibrary);

    const newEntry: SillyTavern.FlattenedWorldInfoEntry = {
      uid: entryIndex >= 0 ? worldInfo.entries[entryIndex].uid : Date.now(),
      displayIndex: entryIndex >= 0 ? worldInfo.entries[entryIndex].displayIndex : 0,
      comment: '头像库数据（绿灯禁用，不进入AI提示词）',
      disable: true, // 禁用条目，不进入提示词
      constant: true,
      selective: false,
      selectiveLogic: 0,
      key: ['头像库数据'],
      keysecondary: [],
      scanDepth: null,
      vectorized: false,
      position: 0,
      role: null,
      depth: 0,
      order: 0,
      content: yamlContent,
      useProbability: false,
      probability: 0,
      excludeRecursion: false,
      preventRecursion: false,
      delayUntilRecursion: false,
      sticky: null,
      cooldown: null,
      delay: null,
      extra: {},
    };

    if (entryIndex >= 0) {
      worldInfo.entries[entryIndex] = newEntry;
    } else {
      worldInfo.entries.push(newEntry);
    }

    await SillyTavern.saveWorldInfo(worldInfoName, worldInfo);
    console.log('保存头像库到世界书成功');
  } catch (error) {
    console.error('保存头像库失败:', error);
  }
}

/**
 * 将AvatarLibrary对象转换为YAML格式字符串
 */
function convertToYaml(library: AvatarLibrary): string {
  let yaml = '# 头像库数据\n';
  yaml += '# 这个世界书条目用于存储角色头像信息，绿灯禁用不进入AI提示词\n';
  yaml += '# 前端界面会直接读写这个世界书条目来获取和保存头像数据\n\n';
  yaml += '头像库数据:\n';

  for (const [characterName, avatars] of Object.entries(library)) {
    yaml += `  ${characterName}:\n`;
    for (const [avatarId, url] of Object.entries(avatars)) {
      yaml += `    ${avatarId}: "${url}"\n`;
    }
  }

  return yaml;
}

export async function loadAvatarLibrary(): Promise<AvatarLibrary> {
  await initAvatarLibrary();
  return avatarLibrary;
}

export function getAvatarLibrary(): AvatarLibrary {
  return avatarLibrary;
}

export function getCharacterAvatarLibrary(characterName: string): Record<number, string> {
  return avatarLibrary[characterName] || avatarLibrary.default;
}

export function getAvatarUrl(characterName: string, avatarId: number): string {
  const charAvatarLibrary = getCharacterAvatarLibrary(characterName);
  return charAvatarLibrary[avatarId] || '';
}

export async function addAvatar(characterName: string, avatarId: number, url: string): Promise<void> {
  if (!avatarLibrary[characterName]) {
    avatarLibrary[characterName] = {};
  }
  avatarLibrary[characterName][avatarId] = url;
  await saveAvatarLibrary();
}

export async function deleteAvatar(characterName: string, avatarId: number): Promise<void> {
  if (avatarLibrary[characterName] && avatarLibrary[characterName][avatarId] !== undefined) {
    delete avatarLibrary[characterName][avatarId];
    await saveAvatarLibrary();
  }
}

export function getNextAvatarId(characterName: string): number {
  const charAvatarLibrary = getCharacterAvatarLibrary(characterName);
  const existingIds = Object.keys(charAvatarLibrary)
    .map(Number)
    .filter(id => id > 0);
  if (existingIds.length === 0) {
    return 1;
  }
  return Math.max(...existingIds) + 1;
}

export async function clearAvatarLibrary(): Promise<void> {
  avatarLibrary = { ...defaultAvatarLibrary };
  await saveAvatarLibrary();
}

export function exportAvatarLibrary(): string {
  return JSON.stringify(avatarLibrary, null, 2);
}

export async function importAvatarLibrary(jsonString: string): Promise<boolean> {
  try {
    const imported = JSON.parse(jsonString);
    if (typeof imported === 'object' && imported !== null) {
      avatarLibrary = { ...defaultAvatarLibrary, ...imported };
      await saveAvatarLibrary();
      return true;
    }
    return false;
  } catch (error) {
    console.error('导入头像库失败:', error);
    return false;
  }
}

// 初始化
initAvatarLibrary();
